.button {
    display: inline-block;
    padding: 10px 20px 10px 20px;
    border-radius: 10px;
    border: none;
    font-size: 20px;
    background-color: #E5E5E5;
    outline: none;
}

.animation-opacity {
    transition: opacity .3s ease-out;
}

.animation-opacity:hover {
    opacity: 0.7;
}
